import React, { useRef, useEffect } from "react"
import mapboxgl from "!mapbox-gl"

const Map3D = ({accessToken, mapStyle, longitude, latitude, flyToEffect}) => {
    mapboxgl.accessToken = accessToken

    const mapContainer = useRef(null)
    const coordinates = [longitude, latitude]

    var start = {
        center: coordinates,
        zoom: 10,
        pitch: 0
    }
    var target = {
        center: coordinates,
        zoom: 17.1,
        pitch: 50
    }

    if (flyToEffect === false) {
        start = target
    }

    useEffect(() => {
        const map = new mapboxgl.Map({
            container: mapContainer.current,
            style: mapStyle,  //from Mapbox Studio used style is "KALMARS Website-used"
            ...start
        })

        const mark = document.createElement('div')
        mark.className = 'location-marker'
        new mapboxgl.Marker({ element: mark, anchor: 'bottom' })
            .setLngLat(coordinates)
            .addTo(map)

        if (flyToEffect === true) {
            map.on('load', () => {
                map.flyTo({
                    ...target,
                    duration: 7000,
                    essential: true
                })
            })
        }
        
    }, [])


    return (
        <div className="css--map-3d h-96" ref={mapContainer}></div>
    )
}

export default Map3D