import React, {useLayoutEffect} from "react";
import anime from "animejs";
import {useTranslation} from "react-i18next";
import Map3D from "./Map3D";
import Calendly from "../../elements/Calendly";

export default function LetsTalk() {
    const {t} = useTranslation();

    useLayoutEffect(() => {
        let timeline = anime.timeline({
            easing: 'linear',
            autoplay: true,
        });
        timeline.add({
            targets: '.container',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            scale: [0.8, 1],
            duration: 1500,
            delay: 500
        });
        timeline.add({
            targets: '.css--fadedtext',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            translateX: ["-100%", 0],
            duration: 500
        });
    }, [])

    return (
        <div className="relative min-h-screen justify-center pt-20 pb-36">
            <div className="md:container md:mx-auto px-4 h-full flex flex-col justify-center md:items-center mt-8">
                <h1 className="text-center text-2xl md:text-5xl glitch" data-glitch={t('contact_us')}>
                    {t('contact_us')}
                </h1>

                <div className="grid grid-cols-1 lg:grid-cols-5 w-full sm:pt-0 lg:py-4 mb-8 lg:mb-0">
                    <div className="lg:col-span-2 md:px-4 w-full sm:mt-0 mb-8 lg:mb-0">
                        <div className="col-lg-4 mt-lg-0">
                            <div itemScope itemType="https://schema.org/Organization" className="section css--letstalk-contacts p-4">
                                <h2 className="mb-3 text-white font-bold">{t('call_us')}</h2>
                                <p itemProp="telephone" className="mb-0">+371 27 79 7931</p>
                                <p itemProp="telephone" className="mb-5">+371 20 09 6655</p>
                                <h2 className="mb-3 text-white font-bold">{t('email_us')}</h2>
                                <p itemProp="email" className="mb-5">info@kalmars.lv</p>
                                <h2 className="mb-3 text-white font-bold">{t('company')}</h2>
                                <p itemProp="name" className="mb-0">SIA Kalmars Group</p>
                                <p className="mb-5">Registration Nr. 40203267964</p>
                                <h2 className="mb-3 text-white font-bold">{t('adress')}</h2>
                                <p itemProp="address" className="mb-5">Riga, Sarlotes street 18A-3, LV-1001</p>
                                <h2 className="mb-3 text-white font-bold">{t('bank')}</h2>
                                <p className="mb-0">Bank account Nr. LV52HABA0551049691987</p>
                                <p className="mb-0">Bank: AS SWEDBANK</p>
                                <p className="mb-0">SWIFT code: HABALV22</p>
                                <p className="mb-0">VAT Nr: <span itemProp="vatID">LV40203267964</span></p>
                            </div>
                        </div>
                    </div>

                    <div className="lg:col-span-3 h-full lg:block justify-self-center lg:justify-self-end md:px-4 w-full">
                        <Calendly calendlyURL={"https://calendly.com/kalmars-meeting/30min?embed_domain=kalmars.lv&embed_type=Inline&hide_event_type_details=1&background_color=0b0c15&text_color=ffffff&primary_color=2fc7e5"} bonusClass={"lg:ml-auto lg:mr-0 mx-auto"} />
                    </div>
                </div>

                <div className="pt-0 lg:pt-4 md:px-4 w-full">
                    <Map3D accessToken={'pk.eyJ1Ijoia2FsdmlzYW4iLCJhIjoiY2xhYXdhcHQ4MDcweTNvbjFueGw4NHlsciJ9.c18OWa3v2R-s_g6jiIF5Hw'} mapStyle={'mapbox://styles/kalvisan/clagpkwh4006x14nsmjwnutwc'} longitude={24.130012362878453} latitude={56.96498776289796} flyToEffect={true} />
                </div>
            </div>
        </div>
    )
}
