import React from 'react'

export default function Calendly({calendlyURL, bonusClass}) {
    let iframeClassName = `css--calendly-iframe ${bonusClass}`

    return (
        <>
            <iframe className={iframeClassName} src={calendlyURL} title="Book a meeting with us" minWidth={'320px'} height={'100%'}></iframe>
            <script type="text/javascript" src="https://assets.calendly.com/assets/external/widget.js" async></script>
        </>
    )
}
