import '../i18n/i18n';

import React from "react"
import FadedText from "../components/elements/FadedText";
import Navigation from "../components/layout/Navigation";
import LetsTalk from "../components/layout/ContactUs/LetsTalk";
import {SEO} from "../components/seo";

const ContactUs = () => {
    return (
        <main className="main-wrapper">
            <Navigation pageNr={'U58KL'}/>
            <FadedText text={'EXPAND'} direction="left"/>

            <LetsTalk/>
        </main>
    )
}
export default ContactUs;
export const Head = () => <SEO title={"Contact Us"} description={'If you have any questions or suggestions, please feel free to contact us, schedule a meeting, or stop by our office!'} keywords={'software development, Iot, web development'}/>
